import { useParams, useLocation } from "react-router-dom";

export const UrlParameter = () => {
  // URLパラメーターを取得できるHook（フック）がuseParams
  const { id } = useParams();
  // クエリパラメーターを取得できるuseLocation
  // 分割代入でsearchにクエリ情報（?name=hogehoge）が入る
  const { search } = useLocation();
  // 引数に設定したクエリパラメーターを便利に扱う為のメソッドを提供してくれる
  // JavaScript標準機能URLSearchParams
  const query = new URLSearchParams(search);

  return (
    <div>
      <h1>UrlParameterページです</h1>
      {/* 上で定義したidを表示できる */}
      <p>パラメーターは {id} です</p>
      {/* URLSearchParamsの機能の一つgetを用いることで引数に設定したkeyの値を取得・表示できる */}
      <p>クエリパラメーターは {query.get("name")} です</p>
    </div>
  );
};
